:export{
  primary:#40c297;
  dark:#222;
  light:#f6f6f6;
  white:#fff;
  fontH6:20;
  fontDefaultSize:12
}

html,body{
  height:100%
}

body{
  font-size:1.05vw;
  line-height:1.5;
  font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale
}

@media screen and (-webkit-min-device-pixel-ratio: 3){
  body{
    font-size:15
  }

  body h6{
    font-size:20
  }

  body p{
    font-size:18
  }

  body span{
    font-size:15
  }
}

a{
  text-decoration:none
}

.aside{
  color:#f6f6f6;
  background-color:#40c297;
  padding:2.79vw;
  padding-right:0;
  grid-gap:1.4vw;
  gap:1.4vw;
  box-sizing:border-box
}

footer{
  display: grid;
  grid-auto-flow: column;
  align-items: center;
  font-weight: 700;
  font-size:1.05vw;
  grid-template-columns:-webkit-max-content;
  grid-template-columns:max-content;
  grid-gap:.7vw;
  gap:.7vw
}

nav{
  display: grid;
  grid-gap:1.4vw;
  gap:1.4vw;
  grid-auto-rows:minmax(-webkit-min-content, -webkit-max-content);
  grid-auto-rows:minmax(min-content, max-content)
}

nav a{
  font-size:1.4vw;
  line-height:2.4;
  border-right:-0.01vw solid #fff
}

nav a.active{
  font-weight: 700;
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
  border-right:.7vw solid #fff
}

#root{
  height:inherit
}

nav a,#landing a{
  color:#f6f6f6;
  transition:all ease-in-out .3s
}

nav a:hover, #landing a:hover {
  --tw-text-opacity: 1;
  color: rgba(255, 255, 255, var(--tw-text-opacity));
}

#body .wrapper, #landing .wrapper {
  display: grid;
  grid-auto-flow: column;
}

#body .wrapper,#landing .wrapper {
  height:inherit;
}

#body{
  background-color:#f6f6f6
}

#body .wrapper{
  grid-template-columns:.3fr 1fr
}

#landing{
  color:#fff;
  background-color:#40c297
}

#landing .wrapper{
  grid-auto-columns:1fr .3fr;
  grid-gap:1.4vw;
  gap:1.4vw
}

@media (min-width: 640px){
  #landing .wrapper{
    grid-auto-columns:1fr .3fr
  }
}

#landing .wrapper__left{
  display: grid;
  grid-auto-flow: row
}

@media (min-width: 640px) {
  #landing .wrapper__left {
    grid-auto-flow: column;
  }
}

#landing .wrapper__left {
  padding-left:2.79vw;
  grid-template-columns:.5fr;
  grid-template-rows:minmax(-webkit-min-content, -webkit-max-content);
  grid-template-rows:minmax(min-content, max-content);
  grid-gap:2.79vw;
  gap:2.79vw;
}

@media (min-width: 640px){
  #landing .wrapper__left{
    grid-template-columns:repeat(4, 0.25fr)
  }
}

#landing .wrapper__left a{
  padding-top:1.4vw;
  margin-top:1.4vw;
  transition:all ease-in-out .3s;
  box-shadow:0 0 0 0 #fff
}

#landing .wrapper__left a:hover{
  box-shadow:0 -0.36vw 0 0 #fff
}

#landing .wrapper__right{
  color:#222;
  background-color:#fff;
  -ms-writing-mode:tb-rl;
  writing-mode:vertical-rl;
  -webkit-text-orientation:mixed;
  text-orientation:mixed;
  padding-right:33.333%;
  padding-top:2.79vw;
  box-sizing:border-box;
  display:grid;
  grid-auto-flow:column
}

#landing .keyvisual{
  position: absolute;
  width: 33.333333%;
  text-align: right;
  font-size:5.57vw;
  color:#222;
  right:8%;
  bottom:2.79vw;
  display:grid;
  justify-content:flex-end
}

#landing .keyvisual__photo, #landing .keyvisual__photo img {
  position: relative;
  width: 100%;
}

#landing .keyvisual__photo,#landing .keyvisual__photo img {
  max-width:50vh;
  max-height:100%;
  z-index:1;
  justify-self:flex-end;
}

#landing .keyvisual__photo:after, #landing .keyvisual__photo img:after {
  position: absolute;
  right: 0px;
  bottom: 0px;
  display: block;
}

#landing .keyvisual__photo:after,#landing .keyvisual__photo img:after {
  width:inherit;
  height:calc(100% - 2.09vw);
  border:.7vw solid #40c297;
  content:"";
}

#landing .keyvisual__photo img{
  margin-left:-1.4vw;
  margin-bottom:1.4vw
}

#about{
  display: grid;
  height: 100%;
  overflow-y: scroll;
  padding:2.79vw;
  box-sizing:border-box
}

@media (min-width: 640px){
  #about{
    grid-template-rows:minmax(-webkit-min-content, -webkit-max-content);
    grid-template-rows:minmax(min-content, max-content)
  }
}

#about img{
  box-shadow:0 5px 15px rgba(34,34,34,.1)
}

@media (min-width: 640px){
  #about img{
    max-width:calc(50% - 2.79vw)
  }
}

#about .bottom{
  display: grid;
  grid-auto-flow: row;
  margin-top:2.79vw;
  grid-gap:2.79vw;
  gap:2.79vw;
  grid-template-rows:1fr;
  grid-template-areas:initial
}

@media (min-width: 640px){
  #about .bottom{
    grid-template-rows:repeat(2, 1fr);
    grid-template-areas:"b d" "c d"
  }
}

#about .link-card:not(:last-child) ul{
  list-style:none
}

#about .link-card:not(:last-child) ul ul{
  padding-top:.7vw;
  padding-bottom:.7vw
}

#about .link-card:last-child{
  grid-area:initial
}

@media (min-width: 640px){
  #about .link-card:last-child{
    grid-area:d
  }
}

.grid-list{
  display: grid;
  height: 100vh;
  overflow-y: scroll;
  width:inherit;
  padding:2.79vw;
  box-sizing:border-box;
  grid-column-gap:1.4vw;
  -webkit-column-gap:1.4vw;
  column-gap:1.4vw;
  grid-row-gap:2.79vw;
  row-gap:2.79vw
}

@media (min-width: 640px){
  .grid-list{
    grid-template-columns:repeat(auto-fill, minmax(20.833vw, 0.33fr))
  }
}

.half-section{
  display: grid;
  height: 100vh;
  overflow: hidden;
  padding:2.79vw;
  box-sizing:border-box;
  grid-gap:2.79vw;
  gap:2.79vw
}

@media (min-width: 640px){
  .half-section{
    grid-auto-flow: column;
    grid-template-columns:repeat(2, minmax(0, 1fr))
  }
}

.half-section.pr-0{
  padding-right:0 !important
}

.half-section__sub{
  display: grid;
  padding:-0.01vw;
  grid-gap:1.4vw;
  gap:1.4vw
}

.half-section__left{
  display: flex;
  width: 100%;
  overflow-x: scroll;
  grid-gap:1.4vw;
  gap:1.4vw
}

@media (min-width: 640px){
  .half-section__left{
    display: grid;
    height: 100%;
    flex: none;
    align-content: flex-start;
    overflow-x: hidden;
    overflow-y: scroll;
    width:inherit !important
  }
}

.half-section__left img{
  max-width:100%;
  margin:0 auto
}

@media (min-width: 640px){
  .half-section__left img{
    max-height:50vh
  }
}

.half-section__right{
  display: grid;
  justify-content: space-between;
  overflow-y: scroll;
  grid-gap:1.4vw;
  gap:1.4vw;
  grid-template-columns:1fr
}

.half-section__right .top,.half-section__right .bottom{
  padding-right:1.4vw
}

.half-section__right .top{
  padding-bottom:1.4vw;
  border-bottom:.22vw solid #40c297
}

.link-card h6{
  font-size:1.4vw;
  margin-bottom:1.4vw
}

.link-card p,.link-card li{
  font-size:1.26vw
}

.link-card ul{
  list-style-position: inside;
  list-style-type: disc
}

.link-card ul ul li{
  list-style-position: inside;
  list-style-type:circle;
  margin-left:1.4vw
}

.card{
  transition:all ease-in-out .3s;
  box-shadow:0 0 0 0 #40c297
}

.card:hover{
  box-shadow:0 .36vw #40c297
}

.card a{
  display: block;
  font-weight: 700;
  color:#222;
  font-size:1.4vw;
  line-height:2
}

.card img{
  width:100%;
  box-shadow:0 5px 15px rgba(34,34,34,.15)
}

.tag-list{
  line-height:.3;
  padding-bottom:1em;
  grid-row-gap:1.4vw;
  row-gap:1.4vw
}

@media (min-width: 640px){
  .tag-list{
    line-height:0
  }
}

.tag-list .tag{
  word-wrap:break-word;
  transform:scale(0.8);
  transform-origin:top left;
  display:inline-block
}

.info-box{
  display: inline-grid;
  grid-gap:.7vw;
  gap:.7vw
}

@media screen and (-webkit-min-device-pixel-ratio: 2),screen and (-webkit-min-device-pixel-ratio: 3){
  body{
    font-size:.9375rem
  }

  h6,nav a,.link-card h6,.card a{
    font-size:4.6875rem
  }

  p,.link-card p,li{
    font-size:2.8125rem
  }

  span{
    font-size:0rem
  }
}

